import {
  CustomActionModalProps,
  CustomActionParams,
  ResolvedAction,
} from '../../exports/types';
import {
  CreateActionConfig,
  CustomActionConfig,
  DividerActionConfig,
} from './base';

export type CollectionPageActionConfig = (
  | CreateActionConfig
  | CustomActionConfig
) & {
  collection: {
    collectionId: string;
    entityTypeSource: 'cms';
  };
};

type CollectionPageAction = {
  type: 'action';
  action: {
    item: CollectionPageActionConfig;
  };
  menu?: never;
};

type CollectionPageMenu = {
  type: 'menu';
  menu: {
    label: string;
    items: (CollectionPageActionConfig | DividerActionConfig)[];
  };
  action?: never;
};

export type CollectionPagePrimaryActions =
  | CollectionPageAction
  | CollectionPageMenu;

export type CollectionPageSecondaryActions =
  | CollectionPageAction
  | CollectionPageMenu;

export interface CollectionPageActionParams {
  collectionId: string;
}

export type CustomActionCollectionPageActionParams =
  CustomActionParams<CollectionPageActionParams>;

export type CustomActionCollectionPageActionResolver = (
  params: CustomActionCollectionPageActionParams,
) => ResolvedAction;

export interface CustomActionCollectionPageActionModalProps
  extends CustomActionModalProps {
  actionParams: CollectionPageActionParams;
}

export type CollectionPageOnRowClickActionConfig = CustomActionConfig & {
  item: any;
};

export interface CollectionPageActionOnRowClickParams {
  item: any;
}

export type CustomActionCollectionPageActionOnRowClickParams =
  CustomActionParams<CollectionPageActionOnRowClickParams>;

export type CustomActionCollectionPageActionOnRowClickResolver = (
  params: CustomActionCollectionPageActionOnRowClickParams,
) => ResolvedAction;
